* {
  padding: 0px;
  margin: 0px;
  font-family: sans-serif;
}
.App__Londonimg {
  z-index: -1;
  height: 100%;
  width: 100%;
}
.App__container {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 85%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.App__gradient {
  color: white;
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 45%;
  background: linear-gradient(to bottom, #110505 0%, #99fff300 100%);
  display: flex;
  justify-content: center;
}
.App__logo {
  color: white;
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 150px;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.signin {
  padding: 10px 15px 10px 15px;
  border-radius: 5px;
  margin: 0px 50px 0px 0px;
}
.logo {
  margin: 0px 0px 0px 100px;
  font-size: 70px;
}
.App__container__letsbegin {
  display: flex;
  justify-content: center;
  padding: 18px;
  background-color: white;
  width: 70%;
}
.App__container__letsbegin__location {
  flex-grow: 2;
}
.App__container__letsbegin__group {
  flex-grow: 1;
}
.App__container__letsbegin__date {
  flex-grow: 1;
}
.App__container__letsbegin__interests {
  flex-grow: 5;
}
.Headers {
  width: 42%;
}
.App__slogan{
  position: absolute;
  left: 0px;
  top: 0px;
 width: 100%;
 height: 45%;
 color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#first{
  font-size: 45px;
  padding: 30px 0px 20px 0px;
}
#first span{
  color: green;
  font-weight: 700;
}